import React, {useEffect,useState} from 'react';
import axios from 'axios';
import "./Listtask";
import { useHistory } from "react-router-dom";


function GetTodos(){
    
    let history = useHistory();
    
    const [state,setCount]= useState({
        data:[{task:"",
        description:""},
             {task:"",
        description:""}]
        
    });
 
useEffect(() => {
    myfn()
    console.log("Req done")
},[])
    

    
function myfn() {
    
    console.log("Hello");
    //alert("Listed in console");
axios({
       method: "get",
       url:"https://5ff9d9b217386d0017b5226b.mockapi.io/task"
        
  })
        .then((response)=>{setCount(response)
                           console.log(response.data);
                           
                           
                          })
    .catch(err => console.error(err));
    
    //console.log(state.data[1].task);
    
    }
    
function pr(ds){
    
    
    
    return (
    <div>
        <div><h4>{ds.task}</h4> 
        <i>Due Date : {ds.duedate}</i></div>
        
        <div>
        <p>{ds.description}</p>
        </div>
    </div>
        )
}
 
function print(){
     history.push('/addtasks');
}


 
    return(
        <div className="container">
        <h2>Tasks to do</h2>
        
        {/*<button onClick={myfn}>List tasks in console</button>*/}
        <div><br/></div>
        {/*<h1>{state.data[1].description}</h1>*/}
        {/*<h1>{typeof(state.data)}</h1>*/}
        {state.data.map(pr)}
       <br/><br/><br/><br/>
           <button onClick={print}>Add Task</button>   
<br/><br/><br/><br/><br/>
        
        
        </div>
        
    )
    
}

export default GetTodos;