import React, {useState} from 'react';
import axios from 'axios';
import { useHistory } from "react-router-dom";

function Addtask(props) {
    
    let history = useHistory();
    
  const [state,setState] = useState({
      
      task :"",
      description :"",
      duedate:'',
      
            
  })
    
  const sendDetailsToServer = () => {
    if(state.task.length && state.description.length){    
      console.log("sending")
      alert("Task Added!");
    
      const payload={
            "task":state.task,
            "description":state.description,
          "duedate":new Date(),
            }
      
      axios.post("https://5ff9d9b217386d0017b5226b.mockapi.io/task", payload);
     
        
                    
    }
      else{
           console.log("Scene aanu");
           
      }
  }
  
  const handleChange = (e) => {
        const {id , value} = e.target   
        setState(prevState => ({
            ...prevState,
            [id] : value
        }))
    }
    
 const handleSubmit = (e) => {
        e.preventDefault();
        sendDetailsToServer();
    }
    
    function print(){
         history.push('/tasks');
    }
    
  return (
    <div className="Addtask">
                 
      <div className="container">
      <h2>Addtask</h2>
      
      <div className="form-group" id ="Addtask">
        
        <form onSubmit = {handleSubmit}>
      
      
      <input type="text" className="form-control" id="task" placeholder="Task" autoComplete="on" defaultValue="" onChange={handleChange}/> <br/>
      <input type="text" className="form-control" id="description" placeholder="Description" autoComplete="on" defaultValue="" onChange={handleChange}/> <br/>
      <input type="submit" value="Submit" />
      
        </form>
      </div>
     <button onClick={print}>List Task</button>                     
      </div>
    </div>
  );
}

export default Addtask;
