import React, {useState} from 'react';
import axios from 'axios';
import './Login.css';
import { useHistory } from "react-router-dom";

function Login() {
    
     let history = useHistory();
    
      const [state,setState] = useState({
      
      email :"",
      password :""
      
            
  })
    
      
     const sendDetailsToServer = () => {
         
         console.log(state);
         
    if(state.email.length && state.password.length){    
      console.log("sending")
        //alert("Logged in");
        
    
      const payload={
            "email":state.email,
            "password":state.password,
          //   "email":"eve.holt@reqres.in",
          // "password":"cityslicka",
            }
      
      axios.post("https://reqres.in/api/login", payload).then((response) => {
  console.log(response);
    console.log("done");
          switch(response.status)
              {
                case 200:
      
            console.log(response.data);
            
            history.push('/userhome')
            break;
      
          default:
              console.log("nope");
            alert("User Doesnt Exist");
            break;
              }
      },
                                                             
          (error) => {
  console.log(error);
          alert("User Doesnt Exist");
        }                                                   
                                                             
                                                             
    );
     
       
        
                    
    }
      else{
          console.log(state.email.length)
           console.log("Error!");
          alert("Enter Valid password!");
           
      }
  }
      
      
      
     const handleChange = (e) => {
        const {id , value} = e.target   
        setState(prevState => ({
            ...prevState,
            [id] : value
        }))
        
    }
    
    const handleSubmit = (e) => {
        e.preventDefault();       
        sendDetailsToServer();
        console.log("logging");
         
        
    }
    
    function print(){
         history.push('/signup');
    }
  
    
  return (
    <div className="Login">
                 
      
      
      <div className ="container">
      <h2>Login</h2>
      <div className="form-group" id ="form_login">
        
        <form onSubmit = {handleSubmit}>

      <input type="email" className="form-control" id="email" placeholder="Enter email" autoComplete="on" defaultValue="" onChange={handleChange}/> <br/>
      <input type="password" className="form-control" id="password" placeholder="Enter Password" autoComplete="on" defaultValue="" onChange={handleChange}/> <br/>
      <input type="submit" value="Submit" />
      
        </form>
      </div>
      <button onClick={print}>Signup</button>                        
      </div>
    </div>
  );
}

export default Login;
