import React, {useState} from 'react';
import axios from 'axios';
import { useHistory } from "react-router-dom";

function Signup(props) {
    
   let history = useHistory();
    
  const [state,setState] = useState({
      
      nickname_signup :"",
      email_signup :"",
      password_signup :"",
      
            
  })
    
  const sendDetailsToServer = () => {
    if(state.email_signup.length && state.password_signup.length){    
      console.log("sending")
        alert("Signed up");
        
    
      const payload={
            "nickname":state.nickname_signup,
            "email":state.email_signup,
            "password":state.password_signup,
            }
      
      axios.post("https://themeshapp.heroku.com/authentication/register", payload);
     
        
                    
    }
      else{
           console.log("Error!");
          alert("Error!");
           
      }
  }
  
  
      function print(){
          history.push('/login');
    }
    
    
  const handleChange = (e) => {
        const {id , value} = e.target   
        setState(prevState => ({
            ...prevState,
            [id] : value
        }))
    }
    
 const handleSubmit = (e) => {
        e.preventDefault();
        sendDetailsToServer();
    }
    

    
  return (
    <div className="Signup">
                 
      
      
      
      <div className="container">
      <h2>SIgnup</h2>
      <div className="form-group" id ="form_signup">
        
        <form onSubmit = {handleSubmit}>
      
      <input type="text" className="form-control" id="nickname_signup" placeholder="Enter Name" autoComplete="on" defaultValue="" onChange={handleChange}/> <br/>
      <input type="email" className="form-control" id="email_signup" placeholder="Enter email" autoComplete="on" defaultValue="" onChange={handleChange}/> <br/>
      <input type="password" className="form-control" id="password_signup" placeholder="Enter Password" autoComplete="on" defaultValue="" onChange={handleChange}/> <br/>
      <input type="submit" value="Submit" />
      
        </form>
      </div>
      <button onClick={print}>Login</button>                        
      </div>
    </div>
  );
}

export default Signup;
